

.app {
  text-align: center;
  margin-top: 60px;
}

.traffic-light {
  width: 50px;
  background-color: #282525;
  padding: 30px;
  border-radius: 20px;
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.light {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  opacity: 0.3;
}

.red {
  background-color: red;
}

.yellow {
  background-color: yellow;
}

.green {
  background-color: rgb(26, 218, 26);
}

.on {
  opacity: 1;
  box-shadow: 0 0 30px 10px rgba(255, 255, 255, 0.6);
}

.controls {
  display: flex;
  justify-content: center;
  gap: 20px;
}

button {
  padding: 10px 30px;
  font-size: 16px;
}
button:hover {
  background-color: #ddd;
  cursor: pointer;
  
}
button:active {
  background-color: #3fcbb3;
}
